.movieCard {
    all:unset;
    cursor: pointer;
    background-size: cover;
    position: relative;
    padding: 1em;
    * {
        position: relative;
        z-index: 1;
    }

    &::before {
        content: '';
        width:100%;
        height: 100%;
        background: rgba(0,0,0,0.4);
        position: absolute;
        top:0;
        right:0;
        bottom:0;
        left:0;
        z-index: 0;


    }
}