body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: calc(7px + 1vw);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navigation {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background: black;
  color: white;
  padding: 2em; }
  .navigation a {
    color: white; }

.movieDetail {
  color: white;
  background-size: cover; }
  .movieDetail .related {
    padding: 2em;
    background: #444; }
  .movieDetail .movie {
    padding: 2em;
    background-size: cover;
    min-height: 100vh;
    color: white;
    position: relative;
    display: grid;
    grid-template-columns: 15em 1fr;
    grid-gap: 2em; }
    @media (max-width: 500px) {
      .movieDetail .movie {
        grid-template-columns: 10em 1fr; } }
    .movieDetail .movie > * {
      position: relative;
      z-index: 1; }
    .movieDetail .movie::before {
      content: '';
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.85);
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 0; }
    .movieDetail .movie .poster {
      width: 100%; }
    .movieDetail .movie .data a {
      color: white; }
    .movieDetail .movie .data .genres span {
      margin-right: 1em;
      background: #444;
      color: white;
      padding: 0.4em; }
    .movieDetail .movie .data .moreInfo div {
      margin: 1em 0; }
    .movieDetail .movie .data .moreInfo .vote_average {
      font-weight: 900; }
    .movieDetail .movie .data .moreInfo .big {
      font-size: 2em; }

.movieDisplay {
  background: #444; }
  .movieDisplay .movies {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 2em;
    color: white; }

.movieCard {
  all: unset;
  cursor: pointer;
  background-size: cover;
  position: relative;
  padding: 1em; }
  .movieCard * {
    position: relative;
    z-index: 1; }
  .movieCard::before {
    content: '';
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 0; }

.MovieList {
  padding: 2em;
  background: #444;
  color: white; }

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh; }
  .app .main {
    -webkit-flex: 1 1;
            flex: 1 1; }

